          //   break;
          // case reportLabelId.MisconductType:
          //   misconductType = msgItem.compVal;
          //   break;
          default:
        }
      }
      return {
        nType,
        zoneId,
        beginDate,
        endDate,
        deviceID,
        carType,
        isIncludeLower,
        unitId,
        vehicleId,
        platformId,
        installID,


        pageSize,
