    formPageParams: function(msg, pageIndex, activeName, isSplitPage) {
      // let nType = 0,
      //   beginDate = "",
      //   endDate = "",
      //   zoneId = "",
      //   carType = 0,
      //   isIncludeLower = 1,
      //   sortName = "",
      //   sortOrder = "",
      //   vehicleId = "",
      //   platformId = "",
      //   installID = "",
      //   upLineFlag = "",
