    handleClickExpData: function(msg) {
      //
      const self = this;
      self.curNameIndex = self.activeNameIndex;
      storageOperation.setItem(commonConfig.ctrlId, msg.ctrlId); //ctrlIdsessionStorage
      // 
      const exportVehicleUplineDataParams = self.formPageParams(
        msg.searchCreteria,
        msg.currentPage,
        msg.activeName,
        msg.isSplitPage // - 
      );
